.BankDetailsForm .nextButton {
  background-color: #F28F17 !important;
  border-radius: 52px;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  box-shadow: none;
  width: 200px;
  margin-bottom: 20px !important;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.BankDetailsForm .updateButton {
  background-color: #1793f2 !important;
}


.BankDetailsForm .updateButton:global(.Mui-disabled) {
  background-color: #686a7e !important;
}

:global(.MuiFormControl-root.valid fieldset) {
  border-color: #00fda2;
  border-width: 2px;
}
