.acceptDocumentsFromSellerButton {
  display: inline-block;
  min-width: 100px;
  border-radius: 52px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  margin-left: auto !important;
  margin-right: auto !important;
  color: #FFFFFF;
  background-color: #05BEC0 !important;
  margin-bottom: 0;
  box-shadow: none !important;
}


.acceptDocumentsFromSellerButton:global(.Mui-disabled) {
  background-color: #9bc6c6 !important;
  cursor: not-allowed;
}

@media (max-width: 767px) {
  .acceptDocumentsFromSellerButton {
    font-size: 24px;
  }

}


@media (min-width: 618.9px) {
  .acceptDocumentsFromSellerButton {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

}

@media (max-width: 618px) {
  .acceptDocumentsFromSellerButton {
    width: 100%;
  }

}

