.ReportProblemWithDocuments {
  display: inline-block;
  min-width: 100px;
  border-radius: 52px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  margin-left: auto !important;
  margin-right: auto !important;
  color: #FFFFFF;
  background-color: #d96b6b !important;
  margin-bottom: 0;
  box-shadow: none !important;
}


.ReportProblemWithDocuments:global(.Mui-disabled){
  background-color: #9bc6c6 !important;
  cursor: not-allowed;
}




@media (max-width: 767px) {
  .ReportProblemWithDocuments {
    font-size: 24px;
  }

}

@media (min-width: 618.9px) {
  .ReportProblemWithDocuments {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

}

@media (max-width: 618px) {
  .ReportProblemWithDocuments {
    width: 100%;
  }

}
