.actionButtonsContainer {
  display: block !important;
  width: fit-content;
  margin-top: 25px;
  margin-bottom: 25px;
}

.actionButtonsContainer .actionButtons {
  display: inline-block;
  outline: none;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  background-color: #BEBEBE;
  margin-bottom: 0;
  box-shadow: none !important;
  border-style: solid;
  border-radius: 52px;
  border-color: #BEBEBE;
}

.actionButtonsContainer .approveButton {
  min-width: 100px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  margin-bottom: 0;
  padding: 9px;
  z-index: 51;
}


.actionButtonsContainer .okIcon {
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  color: #BEBEBE;
  background-color: #ffffff;
  border-style: solid;
  border-width: 1px;
  padding-top: 3px;
  padding-bottom: 2px;
  margin-right: -10px;
  min-width: 10px;
  z-index: 50;
}

.actionButtonsContainer .approveButton:focus,
.actionButtonsContainer .okIcon:focus, {
  outline: none;
}

.actionButtonsContainer:hover .approveButton,
.actionButtonsContainer:hover .okIcon,
{
  background-color: #9adbdd;
  color: #ffffff;
}

.actionButtonsContainer.active button {
  margin-top: 10px;
}

.actionButtonsContainer.active .approveButton,
.actionButtonsContainer.active .okIcon,
{
  background-color: #0ee8ea !important;
  border-color: #0ee8ea !important;
}

.actionButtonsContainer.active .okIcon {
  color: #0ee8ea !important;
  background-color: #ffffff !important;
}
