.sendDocumentsToBuyerButton {
  display: block !important;
  min-width: 100px;
  border-radius: 52px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  margin-left: auto !important;
  margin-right: auto !important;
  color: #FFFFFF;
  background-color: #05BEC0 !important;
  margin-bottom: 0;
  box-shadow: none !important;
}


.sendDocumentsToBuyerButton:global(.Mui-disabled) {
  background-color: #9bc6c6 !important;
  cursor: not-allowed;
}
