.uploadDocumentsContainer:global(.MuiDropzoneArea-root) {
  overflow: unset;
}

.uploadDocumentsContainer :global(.MuiDropzoneArea-icon) {
  color: rgb(5, 190, 192);
}


.uploadDocumentsContainer :global(.MuiGrid-spacing-xs-8) {
  width: unset;
  margin: unset;
}

.uploadDocumentsContainer :global(.MuiGrid-grid-xs-4) {
  flex-grow: unset;
  max-width: unset;
  flex-basis: unset;
  width: 128px;
  height: 128px;
}

.uploadDocumentsContainer :global(.MuiDropzonePreviewList-root) {
  margin-bottom: 32px;
}


:global(._loading-overlay-transition-enter-done) {
  z-index: 499 !important;
}

